	onReady() {
		if(this.token === '' || this.token === null){
			uni.reLaunch({
				url:'/pages/user/index'
			})
			return;
		}



		// console.log(Base64.decode(this.token.split('.')[1]))
		let {
			exp,
			data,
			iat
		} = JSON.parse(Base64.decode(this.token.split('.')[1]));
		let {
			email,
			name,
			time
		} = data;
		this.email = email;
		this.userName = `Hi,${name}`;
	},
	methods: {
